import { EntityObject } from "../entity-object";
export class Player extends EntityObject {
    constructor(config, imageService, canvasService) {
        super(config, imageService, canvasService);
        this.pressedDirection = null;
        this.isLeftPressed = false;
        this.isRightPressed = false;
        this.isJumpPressed = false;
        this.hasCollision = true;
        this.setEntityConfig({
            maxSpeed: 350
        });
        this.initEventListeners();
    }
    updateState(fps) {
        switch (this.pressedDirection) {
            case 'left':
                super.moveLeft();
                break;
            case 'right':
                super.moveRight();
                break;
        }
        if (this.isJumpPressed)
            super.jump();
        super.updateState(fps);
    }
    initEventListeners() {
        document.addEventListener('keydown', e => {
            switch (e.key) {
                case 'ArrowRight':
                    this.isRightPressed = true;
                    this.updateDirection();
                    break;
                case 'ArrowLeft':
                    this.isLeftPressed = true;
                    this.updateDirection();
                    break;
                case 'ArrowUp':
                    this.isJumpPressed = true;
                    break;
            }
        });
        document.addEventListener('keyup', e => {
            switch (e.key) {
                case 'ArrowRight':
                    this.isRightPressed = false;
                    this.updateDirection();
                    break;
                case 'ArrowLeft':
                    this.isLeftPressed = false;
                    this.updateDirection();
                    break;
                case 'ArrowUp':
                    this.isJumpPressed = false;
                    break;
            }
        });
    }
    updateDirection() {
        if (this.isLeftPressed && !this.isRightPressed) {
            this.pressedDirection = 'left';
        }
        else if (!this.isLeftPressed && this.isRightPressed) {
            this.pressedDirection = 'right';
        }
        else if (!this.isLeftPressed && !this.isRightPressed) {
            this.pressedDirection = null;
        }
    }
}
